import { connect } from 'react-redux';
import './TasksList.scss';
import { addTask, setTask } from '../../redux/main-reducer';
import TaskItem from './../TaskItem/TaskItem';
import EmptyList from '../EmptyList/EmptyList';
import GreetingsBlock from '../GreetingsBlock/GreetingsBlock';
import IconButton from '@mui/material/IconButton';
import AddCircleIcon from '@mui/icons-material/AddCircle';

const Main = ({
    addTask,
    setTask,
    currentNewTask,
    tasks,
    currentLanguage,
    darkMode
}) => {

    const onchangedCurrentTask = (e) => {
        setTask(e.currentTarget.value);
    }

    const onclickAddTask = () => {
        let date = new Date();
        let minutesBelowTen = `0${date.getMinutes()}`;
        date = `${date.getDay()}/${date.getMonth() + 1}/${date.getFullYear()} ${date.getHours()}:${date.getMinutes() < 10 ? minutesBelowTen : date.getMinutes()}`;
        addTask(currentNewTask, date);
    }

    return (
        <div className='main'>
            <GreetingsBlock currentLanguage={currentLanguage}/>
            <div className={!darkMode 
                ? 'main__newTask' 
                : 'main__newTask main__newTask-dark'}>
                <input className={!darkMode 
                ? 'main__input' 
                : 'main__input main__input-dark'}
                    value={currentNewTask}
                    onChange={onchangedCurrentTask}
                    placeholder={currentLanguage === 'en' ? "Enter ToDo..." : 'Введите задачу'}
                    maxLength={32} />
                <div>
                    <IconButton className='main__button' onClick={onclickAddTask}>
                        <AddCircleIcon className='main__buttonIcon' color='primary' fontSize='large' />
                    </IconButton>
                </div>
            </div>
            <div className='main__row'>
                <div className='main__col'>
                    <h2>{currentLanguage === 'en' ? 'Tasks' : 'Задачи'}</h2>
                    {tasks.map((task, index) => {
                        if (!task.check) {
                            return <TaskItem key={index}
                                id={task.taskID}
                                task={task.taskText}
                                check={task.check}
                                taskDate={task.taskDate} 
                                darkMode={darkMode}/>
                        }
                    })}
                    <EmptyList tasks={tasks} currentLanguage={currentLanguage}/>
                </div>
                <div className='main__col'>
                    <h2>{currentLanguage === 'en' ? 'Complete tasks' : 'Выполненные задачи'}</h2>
                    {tasks.map((task, index) => {
                        if (task.check) {
                            return <TaskItem key={index}
                                id={task.taskID}
                                task={task.taskText}
                                check={task.check}
                                taskDate={task.taskDate} 
                                darkMode={darkMode}/>
                        }
                    })}
                    <EmptyList tasks={tasks} currentLanguage={currentLanguage}/>
                </div>
            </div>
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        currentNewTask: state.main.currentNewTask,
        tasks: state.main.tasks,
        currentLanguage: state.settings.currentLanguage,
        darkMode: state.settings.darkMode
    }
}

export default connect(mapStateToProps, {
    addTask,
    setTask
})(Main);