import './SettingsMode.scss';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import Switch from '@mui/material/Switch';

const SettingsMode = ({
    currentLanguage,
    changeMode,
    darkMode
}) => {

    const handleChange = () => {
        !darkMode ? changeMode(true) : changeMode(false);
    };

    return (
        <Accordion>
            <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls="panel2a-content"
                id="panel2a-header">
                <Typography>
                    {currentLanguage == 'en' ? 'Dark mode' : 'Темная тема'}
                </Typography>
            </AccordionSummary>
            <AccordionDetails>
                <Typography>
                    {currentLanguage == 'en'
                        ? 'You can use dark mode.'
                        : 'Вы можете использовать темную тему'}
                </Typography>

                <Switch onChange={handleChange} checked={darkMode}/>
                {currentLanguage == 'en'
                    ? !darkMode ? 'Light mode' : 'Dark mode'
                    : !darkMode ? 'Светлая тема' : 'Темная тема'}
            </AccordionDetails>
        </Accordion>
    )
}

export default SettingsMode;