import './TaskItem.scss';
import checkIcon from './../../assets/images/check.svg';
import deleteIcon from './../../assets/images/delete.svg';
import { connect } from 'react-redux/es/exports';
import { deleteTask } from '../../redux/main-reducer';
import { useEffect, useMemo, useState } from 'react';
import { checkTask } from '../../redux/main-reducer';
import IconButton from '@mui/material/IconButton';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import RemoveCircleIcon from '@mui/icons-material/RemoveCircle';
import ChangeCircleIcon from '@mui/icons-material/ChangeCircle';

const TaskItem = ({
    task,
    id,
    deleteTask,
    checkTask,
    check,
    taskDate,
    darkMode

}) => {

    const onclickDeleteTask = () => {
        deleteTask(id)
    }

    const onclickCheckTask = () => {
        checkTask(id)
    }

    return (
        <div className={!darkMode ? 'taskItem' : 'taskItem taskItem-dark'}>
            <div className='taskItem__row'>
                <span className={!check ? null : 'taskItem__checked'}>
                    {task}
                </span>
                {!check
                    ? <IconButton onClick={onclickCheckTask}>
                        <CheckCircleIcon color='success' fontSize='large' />
                    </IconButton>
                    : <IconButton onClick={onclickCheckTask}>
                        <ChangeCircleIcon color='action' fontSize='large' />
                    </IconButton>}

                <IconButton onClick={onclickDeleteTask}>
                    <RemoveCircleIcon color='error' fontSize='large' />
                </IconButton>
            </div>
            <div className='taskItem__date'>
                {taskDate}
            </div>
        </div>
    )
}

export default connect(null, {
    deleteTask,
    checkTask
})(TaskItem);