const CHANGE_BG = 'settings/CHANGE_BG';
const CHANGE_LANGUAGE = 'settings/CHANGE_LANGUAGE';
const CHANGE_MODE = 'settings/CHANGE_MODE';
const CHANGE_BG_BRIGHTNESS = 'settings/CHANGE_BG_BRIGHTNESS';

let initialState = {
    currentBG: 1,
    currentLanguage: 'en',
    darkMode: false,
    brightnessBG: 5
}

const settingsReducer = (state = initialState, action) => {
    switch (action.type) {

        case CHANGE_BG:
            return {
                ...state,
                currentBG: action.bgID
            }

        case CHANGE_LANGUAGE:
            return {
                ...state,
                currentLanguage: action.language
            }

            case CHANGE_MODE:
                return {
                    ...state,
                    darkMode: action.modeValue,
                }

        case CHANGE_BG_BRIGHTNESS:
            return {
                ...state,
                brightnessBG: action.brightnessID
            }
        
            
        default:
            return state;
    }
}

export const changeBG = (bgID) => ( {type: CHANGE_BG, bgID} );
export const changeLanguage = (language) => ( {type: CHANGE_LANGUAGE, language} );
export const changeMode = (modeValue) => ( {type: CHANGE_MODE, modeValue} );
export const changeBrightnessBG = (brightnessID) => ( {type: CHANGE_BG_BRIGHTNESS, brightnessID} );

export default settingsReducer;