import './Settings.scss';
import { connect } from 'react-redux';
import { changeBG, changeLanguage, changeMode } from '../../redux/settings-reducer';
import SettingsBackground from '../SettingsBackground/SettingsBackground';
import SettingsLanguage from '../SettingsLanguage/SettingsLanguage';
import SettingsMode from '../SettingsMode/SettingsMode';

const Settings = ({
    changeBG, 
    changeLanguage,
    currentLanguage,
    changeMode,
    darkMode
}) => {
    return (
        <div className={!darkMode ? 'settings' : 'settings settings-dark'}>
            <SettingsBackground changeBG={changeBG} currentLanguage={currentLanguage}/>
            <SettingsLanguage changeLanguage={changeLanguage} currentLanguage={currentLanguage}/>
            <SettingsMode changeMode={changeMode} darkMode={darkMode} currentLanguage={currentLanguage}/>
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        currentLanguage: state.settings.currentLanguage,
        darkMode: state.settings.darkMode
    }
}

export default connect(mapStateToProps, {
    changeBG,
    changeLanguage,
    changeMode
})(Settings);