const ADD_TASK = 'main/ADD_TASK';
const SET_TASK = 'main/SET_TASK';
const DELETE_TASK = 'main/DELETE_TASK';
const CHECK_TASK = 'main/CHECK_TASK';

let initialState = {
    currentNewTask: '',
    tasks: [],
    counterID: 0,
}

const mainReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_TASK:
            return {
                ...state,
                currentNewTask: action.currentTask
            }

        case ADD_TASK:
            return {
                ...state,
                counterID: state.counterID + 1,
                tasks: [...state.tasks, 
                    // action.taskText
                    {
                        taskID: state.counterID, 
                        taskText: action.taskText,
                        taskDate:action.taskDate,
                        check: false
                    }
                ], 
                currentNewTask: ''
            }

        case DELETE_TASK:
            return {
                ...state,
                tasks: [...state.tasks.filter(task => task.taskID !== action.taskID)]
            }

        case CHECK_TASK:
            return {
                ...state,
                tasks: [...state.tasks.map(task => {
                    if (task.taskID == action.taskID) {
                        if (task.check == false) {
                            return {
                                ...task, check: true
                            }
                        }
                        else {
                            return {
                                ...task, check: false
                            }
                        }
                    }
                    return task
                })]
            }     
            
        default:
            return state;
    }
}

export const setTask = (currentTask) => ( {type: SET_TASK, currentTask} );
export const addTask = (taskText, taskDate) => ( {type: ADD_TASK, taskText, taskDate} );
export const deleteTask = (taskID) => ( {type: DELETE_TASK, taskID} );
export const checkTask = (taskID) => ( {type: CHECK_TASK, taskID} );

export default mainReducer;