import './App.scss';
import TasksList from './components/TasksList/TasksList';
import './nullstyle.scss';
import Menu from './components/Menu/Menu';
import Stats from './components/Stats/Stats';
import Settings from './components/Settings/Settings';
import { Route, Routes, Navigate } from "react-router-dom";
import { connect } from 'react-redux';

const App = ({
  currentBG,
  currentLanguage,
  darkMode
}) => {

  return (
    <div className="app">
      <div className={`wallpaper wallpaper${currentBG}`}></div>
      <div className='app__row'>
        <Menu currentLanguage={currentLanguage} darkMode={darkMode}/>
        <Routes>
          <Route path='/home' element={<TasksList />}></Route>
          <Route path='/stats' element={<Stats />}></Route>
          <Route path='/settings' element={<Settings />}></Route>
          <Route path='*' element={<Navigate to="/home" replace />}></Route>
        </Routes>
      </div>
    </div>
  );
}

const mapStateToProps = (state) => {
  return {
    currentBG: state.settings.currentBG,
    currentLanguage: state.settings.currentLanguage,
    darkMode: state.settings.darkMode
  }
}

export default connect(mapStateToProps, {

})(App);
